import React, { PropTypes } from 'react';
import { Link } from 'react-router';

import InfosBlock from '../components/InfosBlock';
import { Wrapper, Block, Title2, Button3, Header2, Logo, Center, P } from '../components/styledComponents';
import LogoImg from '../assets/logo1.png';

const renderSuccess = () => (
  <div>
    <Title2>Votre inscription et votre commande ont bien t prises en compte</Title2>
    <Center>
      <Link to={'/'}><Button3>{"Revenir vers la page d'accueil"}</Button3></Link>
    </Center>
  </div>
);

const Step5 = ({ submit, isLoading, done, error }) => (
  <Wrapper>
    <Block id="infos">
      <Header2>
        <Logo src={LogoImg} />
        {!done &&
          <Title2>Mes informations</Title2>
        }
      </Header2>
      {isLoading && <Center><P>Chargement</P></Center>}
      {done && renderSuccess()}
      {error && <Center><P>Une erreur a eu lieu</P></Center>}
      {!done && !isLoading && !error && <InfosBlock total={0} submit={submit} />}
    </Block>
  </Wrapper>
);

Step5.propTypes = {
  submit: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  done: PropTypes.bool,
  error: PropTypes.bool,
};

Step5.defaultProps = {
  isLoading: false,
  done: false,
  error: false,
};

export default Step5;



